<template>
	<div class="page2_1_1">
		<div class="page-top">
			<Place class="fl" :data="msg"/>
			<div class="fr inter">
				<interBtn title1="合规审计" title="政策法规疑点" id="more2_1_1" ydname="审计疑点线索挖掘"/>
			</div>
		</div>
		<div class="item-body">
			<el-dialog :visible.sync="dialogVisible" width="50%" class="xqModel">
				<div class="loading" v-if="xqModel[0]=='null'">
			        <span class="el-icon-loading"></span>
			    </div>
				<div v-else v-for="item in xqModel" class="flxq">
					<div v-if="item.icon">
						<div class="title">{{item.icon}}</div>
				        <div class="decs"><span class="time">{{item.time}}</span> &nbsp;&nbsp;&nbsp;<span class="form">{{item.desc}}</span>&nbsp;&nbsp;&nbsp;关键词：<span class="key" style="color: red">{{item.key}}</span></div>
				        <p class="cont" v-html="item.text"></p>
					</div>
					<div v-else-if="item.url">
						<div class="title">{{item.title}}</div>
				        <div class="decs"><span class="time">{{item.time_str}}</span> &nbsp;&nbsp;&nbsp;<span class="form">{{item.laiyuan}}</span>&nbsp;&nbsp;&nbsp;关键词：<span class="key" style="color: red">{{item.key}}</span></div>
				        <p class="cont" v-html="item.titl"></p>
					</div>
					<div v-else-if="item.cwzb_name" style="overflow-x: auto;">
						<div class="cwzbTitle">{{cwzbTitle}}</div>
						<table class="cw-tb" cellspacing="0" cellpadding="0">
							<tr class="cw-tr">
								<th class="f"><p style="text-align: right;">时间</p><p style="text-align: left;">财务指标</p></th>
								<td v-for="time in xqModel[0].time">{{time}}</td>
							</tr>
							<tr  class="cw-tr" v-for="item in xqModel">
								<th>{{item.cwzb_name}}（{{item.danwei}}）</th>
								<td v-for="cwsj in item.cwsj">{{cwsj}}</td>
							</tr>
						</table>
					</div>
				</div>
			</el-dialog>
            <div class="ej-item">
            	<div id="xsd_main" style="display: block;margin: 0 auto;margin-bottom: -20px; width:200px;height: 200px"></div>
                <el-row :gutter="50">
                	<el-col :span="12">
                		<div class="tree-head">
                        	<span class="word-name" style="color: #b80000;font-weight: bold;">{{getWord}}</span> / <span>主题词和财务指标间关联关系</span> <span style="color: #b80000;font-weight: bold;">（ 法律法规 ）</span>
                        </div>
                        <div id="trees-box" style="width: 90%;margin:0 auto">
	                        <div id="tree-box" class="tree-box">
	                        	<TreeD3 id="Fagui" :apiTree="apiFaguiTree"/>
	                        </div>
	                        <div class="box" style="margin-bottom:20px">
	                        	<div class="my-panel">
		                            <div class="title">{{getFaguiNode}} / 法律法规</div>
		                            <div class="scroll">
		                            	<div class="loading" v-if="getFaguiList[0]=='null'">
		                            		 <span class="el-icon-loading"></span>
		                            	</div>
		                            	<img style="display:block;margin: 0 auto;" v-else-if="getFaguiList.length<=0" src="../../../assets/no-sj.png">
		                                <ul v-else class="list" style="max-height: 430px;"> 
		                                	<li class="over" v-for="item in getFaguiList">
		                                        <div class="list-intr">
		                                            <div class="title">
		                                            	<a @click="getXqData('faguiXq',item.icon)">◆ 《 
		                                            		<span class="icon_name">{{item.icon}}
		                                            		</span> 》
		                                                </a>
		                                            </div>
		                                            <div class="over">
		                                                <div class="fl type">{{item.desc}}&nbsp;&nbsp;&nbsp;</div>
		                                                <div class="time fr">{{item.time}}</div>
		                                            </div>
		                                        </div>
		                                    </li> 
		                                </ul>
									</div>
		                        </div>
	                        </div>
	                        <div class="box">
	                        	<div class="my-panel">
		                            <div class="title">{{getFaguiNode}} / 财务报表</div>
		                            <div class="scroll">
		                            	<div class="loading" v-if="getFaguiCWList[0]=='null'">
		                            		 <span class="el-icon-loading"></span>
		                            	</div>
		                            	<img style="display:block;margin: 0 auto;" v-else-if="getFaguiCWList.length<=0" src="../../../assets/no-sj.png">
		                                <ul v-else class="list" style="max-height: 430px;"> 
		                                	<li class="over" v-for="item in getFaguiCWList">
		                                        <div class="list-intr">
		                                            <div class="title">
		                                                <a @click="getXqData('cwbbXq',[item.bmc,item.cwzb])"> 
		                                                	◆ <span class="text">{{item.bmc}}</span>
		                                               	</a>
		                                            </div>
		                                        </div>
												<div class="hidden list-hidden">{{item.cwzb}}</div>
		                                    </li>
		                                </ul>
									</div>
                        		</div>
	                        </div>
                        </div>
                	</el-col>
                	<el-col :span="12">
                		<div class="tree-head">
                        	<span class="word-name" style="color: #b80000;font-weight: bold;">{{getWord}}</span> / <span>主题词和财务指标间关联关系</span> 
                        	<span style="color: #e67e17;font-weight: bold;">（ 新闻媒体 ）</span>
                        </div>
                        <div id="trees2-box" style="width: 90%;margin:0 auto">
	                        <div id="tree2-box" class="tree-box">
								<TreeD3 id="Xinwen" :apiTree="apiXinwenTree"/>
	                        </div>
	                        <div class="box" style="margin-bottom:20px">
								<div class="my-panel">
		                            <div class="title">{{getXinwenNode}} / 新闻媒体</div>
		                            <div class="scroll">
		                            	<div class="loading" v-if="getXinwenList[0]=='null'">
		                            		 <span class="el-icon-loading"></span>
		                            	</div>
		                            	<img style="display:block;margin: 0 auto;" v-else-if="getXinwenList.length<=0" src="../../../assets/no-sj.png">
		                                <ul v-else class="list" style="max-height: 430px;"> 
		                                	<li class="over" v-for="item in getXinwenList" :id="item.url">
		                                        <div class="list-intr">
		                                            <div class="title">
		                                            	<a @click="getXqData('xwXq',item.url)">◆ {{item.title}}
		                                                </a>
		                                            </div>
		                                            <div class="over">
		                                            	<div class="fl time">来源：{{item.laiyuan}}&nbsp;&nbsp;&nbsp;</div>
		                                                <span class="fr time">发布时间：{{item.time_str}}</span>
		                                            </div>
		                                        </div>
		                                    </li> 
		                                </ul>
									</div>
		                        </div>
	                        </div>
	                        <div class="box">
	                        	<div class="my-panel">
		                            <div class="title">{{getXinwenNode}} / 财务报表</div>
		                            <div class="scroll">
		                            	<div class="loading" v-if="getXinwenCWList[0]=='null'">
		                            		 <span class="el-icon-loading"></span>
		                            	</div>
		                            	<img style="display:block;margin: 0 auto;" v-else-if="getXinwenCWList.length<=0" src="../../../assets/no-sj.png">
		                                <ul v-else class="list" style="max-height: 430px;"> 
		                                	<li class="over" v-for="item in getXinwenCWList">
		                                        <div class="list-intr">
		                                            <div class="title">
		                                                <a @click="getXqData('cwbbXq',[item.bmc,item.cwzb])"> 
		                                                	◆ <span class="text">{{item.bmc}}</span>
		                                               	</a>
		                                            </div>
		                                        </div>
												<div class="hidden list-hidden">{{item.cwzb}}</div>
		                                    </li>
		                                </ul>
									</div>
                        		</div>
	                        </div>
                        </div>
                	</el-col>
                </el-row>          
            </div>
        </div>
	</div>
</template>
<script type="text/javascript">
	import Place from './place'
	import interBtn from './../interactions'
	import reqData from "../../../reqData.js"
	import * as echarts from 'echarts'
	import TreeD3 from '../../D3'
	export default{
		name:'page2_1_1',
		data(){
			return{
				dialogVisible: false,
				msg:["审计疑点线索挖掘","合规审计","与政策法规的比较"],
				xsdData:0,
				xqModel:[],
				cwzbTitle:''
			}
		},
		components:{
			Place,
			TreeD3,
			interBtn
		},
		computed:{
			apiFaguiTree(){
				return {apiName: "faguiTree",data:{yidian_name:this.getWord,ID:this.company.id}}
			},
			apiXinwenTree(){
				return {apiName: "xinwenTree",data:{name:this.getWord,ID:this.company.id,time:1}}
			},
			company(){
				return JSON.parse(this.$store.getters.getCompany)||JSON.parse(sessionStorage.getItem("company"));
			},
			userName(){
				return this.$store.getters.getUser||sessionStorage.getItem("username");
			},
			getWord(){
				return this.$store.getters.getWord||sessionStorage.getItem("word");
			},
			optionPie(num){
		        var dataStyle = {
		            normal: {
		                label: {
		                    show: false
		                },
		                labelLine: {
		                    show: false
		                },
		                shadowBlur: 40,
		                shadowColor: 'rgba(40, 40, 40, 0.5)',
		            }
		        };
		        var placeHolderStyle = {
		            normal: {
		                color: 'rgba(44,59,70,0.3)', // 未完成的圆环的颜色
		                label: {
		                    show: false
		                },
		                labelLine: {
		                    show: false
		                }
		            },
		            emphasis: {
		                color: 'rgba(44,59,70,0.3)' // 未完成的圆环的颜色
		            }
		        };
		        return {
		            tooltip: {
		                show: false,
		            },
		            toolbox: {
		                show: false,
		            },
		            // color : ['#3dd4de','#b697cd','#a6f08f'],
		            // backgroundColor: 'rgba(0,0,0,0.8)',
		            series: [{
		                name: 'Pie1',
		                type: 'pie',
		                clockWise: false,
		                radius: [80, 85],
		                itemStyle: dataStyle,
		                hoverAnimation: false,
		                center: ['50%', '50%'],
		                data: [{
		                    value: this.xsdData,
		                    label: {
		                        normal: {
		                            formatter: '相似度\n{d}%',
		                            position: 'center',
		                            show: true,
		                            textStyle: {
		                                fontSize: '35',
		                                fontWeight: 'normal',
		                                color: '#b80000'
		                            }
		                        }
		                    },
		                    itemStyle: {
		                        normal: {
		                            color: '#b80000',
		                            shadowColor: '#b80000',
		                            shadowBlur: 10
		                        }
		                    }
		                }, {
		                    value: 100-this.xsdData,
		                    name: 'invisible',
		                    itemStyle: placeHolderStyle,
		                }]
		            }]
		        }
		    },
		    getFaguiNode(){
		    	let node=this.$store.getters.getFaguiNode||sessionStorage.getItem("word");
		    	// if(node.search(",")>0){
		    	// 	let nodeArr=node.split(",");
		    	// 	node=nodeArr[nodeArr.length-2];
		    	// }

		    	return node;
		    },
		    getXinwenNode(){
		    	let node=this.$store.getters.getXinwenNode||sessionStorage.getItem("word");
		    	// if(node.search(",")>0){
		    	// 	let nodeArr=node.split(",");
		    	// 	node=nodeArr[nodeArr.length-2];
		    	// }
		    	return node;
		    },
		    getFaguiList(){
		    	return this.$store.getters.getFaguiList||JSON.parse(sessionStorage.getItem("faguiList"));
		    },
		    getXinwenList(){
		    	return this.$store.getters.getXinwenList||JSON.parse(sessionStorage.getItem("xinwenList"));
		    },
		    getFaguiCWList(){
		    	return this.$store.getters.getFaguiCWList||JSON.parse(sessionStorage.getItem("faguiCWList"));
		    },
		    getXinwenCWList(){
		    	return this.$store.getters.getXinwenCWList||JSON.parse(sessionStorage.getItem("xinwenCWList"));
		    },
		   
		},
		methods:{
			getPieData(){
				var myChart = echarts.init(document.getElementById("xsd_main"));
				myChart.showLoading();
		        reqData
		            .req({
		                apiName: "xsdPie",
		                data: {             
		                name: this.getWord,
		                id:this.company.id,
		            }
		            })
		            .then(res => {
		                res = res.data;
		                if (res && res.code === "200") {
		                	let xsd = parseFloat(res.data);
		                	 console.log(xsd);
		                	 myChart.hideLoading();
		                	 this.xsdData=xsd;
		                	 
		                	 myChart.setOption(this.optionPie);
		                }
		            })
		            .catch(err => {
		            	console.log(err);
		            });
		    },
		    getXqData(type,data1){
		    	this.dialogVisible=true;
		    	this.xqModel=["null"];
		    	if(type=="faguiXq"){
		    		 reqData
			            .req({
			                apiName: "faguiXq",
			                data: {             
				                icon_name: data1,
				                ID:this.company.id,
				            }
			            })
			            .then(res => {
			                res = res.data;
			                if (res && res.code === "200") {
			                	let data = res.data;
			                	console.log(data);
			                	let node=this.$store.getters.getFaguiNode;
					    		let nodeArr=[];
					    		let keyStr=[];
					    		let cont=data[0].text;
					    		let reg1=new RegExp("。","g");
					    		let reg3=new RegExp(" ","g");
						    	cont=cont.replace(reg1,"。</p><p>");
								cont=cont.replace(reg3,"</p><p>");
						    	if(node.search(",")>0){
						    		nodeArr=node.split(",");
						    	}else{
						    		 nodeArr[0]=node;
						    	}
						    	nodeArr.forEach(function(v,i){
						    		if(v){
						    			keyStr+=(v+",");
						    			let reg2=new RegExp(v,"g");
				                		cont=cont.replace(reg2,"<i style='color:#00a2c9'>"+v+" </i>");
						    		}
					
						    	})
			                	
			                	data[0].text="<p>"+cont+"</p>";
			                	data[0].key=keyStr;
			                	console.log(data);
			                	this.xqModel=data;
			                }
			            })
			            .catch(err => {
			            	console.log(err);
			            });
		    	}else if(type=="xwXq"){
		    		this.xqModel=["null"];
		    		 reqData
			            .req({
			                apiName: "xinwenXq",
			                data: {             
				                url: data1
				            }
			            })
			            .then(res => {
			                res = res.data;
			                if (res && res.code === "200") {
			                	let data = res.data;
			                	console.log(data);
			                	let node=this.$store.getters.getXinwenNode;
					    		let nodeArr=[];
					    		let keyStr=[];
					    		let cont=data[0].titl;
					    		let reg1=new RegExp("。","g");
						    	cont=cont.replace(reg1,"。</p><p>");


						    	if(node.search(",")>0){
						    		nodeArr=node.split(",");
						    	}else{
						    		 nodeArr[0]=node;
						    	}
						    	nodeArr.forEach(function(v,i){
						    		if(v){
						    			keyStr+=(v+",");
						    			let reg2=new RegExp(v,"g");
				                		cont=cont.replace(reg2,"<i style='color:#00a2c9'>"+v+" </i>");
						    		}
					
						    	})
			                	
			                	data[0].titl="<p>"+cont+"</p>";
			                	data[0].key=keyStr;
			                	console.log(data);
			                	this.xqModel=data;
			                }
			            })
			            .catch(err => {
			            	console.log(err);
			            });
		    	}else if(type=="cwbbXq"){
		    		let bmc=data1[0];
		    		let cwzb=data1[1][0];
		    		this.xqModel=["null"];
		    		this.cwzbTitle=bmc;
		    		 reqData
			            .req({
			                apiName: "cwbbXq",
			                data: {             
				                'bmc_name':bmc,
				                'cwzb_name':cwzb,
				                ID:this.company.id,
				            }
			            })
			            .then(res => {
			                res = res.data;
			                if (res && res.code === "200") {
			                	let data = res.data;
			                	console.log(data);
			                	this.xqModel=data;
			                }
			            })
			            .catch(err => {
			            	console.log(err);
			            });
		    	}
		       
		    }
		    
		},
		mounted: function(){
			this.getPieData();
		},

	}
</script>
<style type="text/css" scoped="">
	.loading{
		display: block;
		width: 100%;
		height: 280px;
		line-height: 280px;
		text-align: center;
		font-size: 50px;
		color: #b80000;
	}
	.tree-head{
		text-align: center;
		line-height: 30px;
    	margin-bottom: 10px;
		font-size: 18px;
	}
	.tree-box{
		border: 1px solid rgb( 222, 222, 222 );
	    box-sizing: border-box;
	    box-shadow: 0px 1px 3px 0px rgba( 0, 0, 0 ,0.25);
	    margin-bottom: 20px;
	    min-height: 100px;
	}

        .box .my-panel{
            /*padding: 0 100px;*/
            /*margin-left: -100px;*/
            border: 1px solid #e0e0e0;
            width: 100%;
            height: auto;
            color: #666666;
            font-size: 15px;
            font-family: "微软雅黑";
        }
        #tree-box,#tree2-box,#tree3-box,#tree4-box{
            border: 1px solid rgb( 222, 222, 222 );
