            font-size: 22px;
        }



        /*媒体新闻样式*/
        .box .my-panel .list{
            padding: 20px;
            box-sizing: border-box;
        }
        .box .my-panel .list .list-intr .time,
        .box .my-panel .list .list-intr .type{
            margin-left: 20px;
            color: #999999;
            font-size: 14px;
            height: 22px;
            line-height: 22px;
        }
        .box .my-panel .list li{
            padding: 10px 0;
        }
        .box .my-panel .list .list-intr{
            position: relative;
            width: 100%;
            box-sizing: border-box;
        }

        .box .my-panel .list .list-intr .title a{
            display: block;
            color: #333;
            font-size: 16px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            cursor: pointer;
            height: 30px;
            line-height: 30px;
        }
       .box .my-panel .list .list-intr .title a .list-style{
            color: #7b7b7b;
            font-size: 18px;
        }
        .box .my-panel .list .list-intr .title a:hover
        ,.box .my-panel .list .list-intr .title a:hover .list-style{
            color:#b80000;
        }



		/*弹出框法律详情样式结束*/
        .flxq .title{
		    color: #000;
		    font-size: 28px;
		    line-height: 60px;
		    text-align: center;
		}
		.flxq .decs{
		    color: #999999;
		    font-size: 14px;
		    line-height: 26px;
		    margin-bottom: 26px;
		    text-align: center;
		}
		.flxq .decs .key,
		.xwxq .decs .key{
		    font-size: 15px;
		    color: #00a2c9;
		}
		.flxq .cont{
		    color: #333;
		    font-size: 16px;
		    text-align:justify;
		    text-indent: 40px;
		    line-height: 34px;
		}
		.flxq .cont p{
		    margin-bottom: 16px;
		}
		.flxq .cwzbTitle{
			color: #b80000;
			font-size: 22px;
			text-align: center;
			width: 100%;
		}
		.flxq .cw-tb{
			margin: 20px 10px 50px 10px;
			width: 500%;
			border: 1px solid #ccc;
			max-width: 1000%;
		}
		.flxq .cw-tr{
			height: 64px;
			line-height: 64px;

		}
		.flxq .cw-tr td,.flxq .cw-tr th{
			border: 1px solid #ccc;
			text-align: center;
		}
		.flxq .cw-tb .f{
			line-height: 20px;
			padding: 0 8px;
			box-sizing: border-box;
		}
		/*弹出框法律详情样式结束*/
	
</style>


